

































import { Component, Prop, Vue } from 'vue-property-decorator';
import { Watch } from 'vue-property-decorator';

const debounce = 500;

@Component
export default class SearchBar extends Vue {
	@Prop() private value!: string;

	private focus = false;
	private message = '';
	private debounceTimeout: number | null = null;
	private lastSend = '';

	private get suggestions(): [string, string][] {
		return [];
		/*
		return [
			['by', 'author'],
			['for', 'character'],
			['has', 'style, expressions, etc'],
		];
		*/
	}

	private onFocusIn(event: FocusEvent) {
		console.log(event);
		this.focus = true;
	}

	private onFocusOut(event: FocusEvent) {
		console.log(event);
		this.focus = false;
		console.log(document.activeElement);
	}

	@Watch('value')
	private updateInternalValue() {
		if (this.lastSend === this.value) {
			this.lastSend = '';
			return;
		}
		this.message = this.value;
	}

	private onUpdate() {
		if (this.debounceTimeout) clearTimeout(this.debounceTimeout);
		this.debounceTimeout = setTimeout(this.doUpdate, debounce);
	}

	private doUpdate() {
		if (this.debounceTimeout) clearTimeout(this.debounceTimeout);
		this.debounceTimeout = null;
		const div = document.createElement('div');
		div.innerHTML = this.message;
		this.lastSend = div.innerText;
		this.$emit('input', div.innerText);
	}
}
